body{
  /* background:#120220; */
  background-image: linear-gradient(to right bottom, #220a33, #291549, #2b2261, #262f7b, #0c3e96);
  color: white;
  font-family: 'Poppins', sans-serif;
}

.App{
  display: flex;
  flex-direction: column;
}

.navbar{
  width: 25%;
  margin: 15px auto 0px;
  padding: 5px 2px;
  border-radius: 5px;
  display: flex;
  justify-content:space-evenly;
  background: linear-gradient(135deg,rgba(255, 255, 255, 0.16),rgba(255, 255, 255, 0.041));
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  /* border-radius: 10px; */
}
.navbar a{
  text-decoration: none;
  align-items: center;
  /* color: aliceblue; */
  font-size: 17px;
}

.header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* border: 1px solid white; */
}
.left{
  width: 45%;
  padding:20px 40px;
  margin: 10px 30px;
}
.left h1{
  width: 100%;
  color: rgb(255, 255, 255);
  font-size: 37px;
  font-weight: 500;
}
.left p{
  color: hsl(267, 87%, 76%);
  text-align: justify;
  padding: 0px 0px 20px ;

}

.rigth{
  width: 50%;
}

.globe{
  width: 100%;
  /* border: 1px solid white; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.currency{
  width:87%;
  margin: 12% auto 15px;
  display: flex;
  justify-content: space-between;
  /* border: 1px solid; */
  align-items: center;
  border-radius: 15px;
  color: aliceblue;
  color: rgb(197, 197, 197);

  background: rgb(214,14,213);
  background: linear-gradient(51deg, rgb(185, 8, 185) 0%, rgba(6, 56, 142, 0.895) 100%);
}
.intro-box-left{
  width: 25%;
  /* border: 1px solid; */
}
.intro-box-left img{
  width: 100%;
  /* z-index: 2; */
  margin-top: -170px;
  margin-left: -20px;
  margin-bottom: -60px;
}

.intro-box-right{
  width: 65%;
  margin-right: auto;
}
.intro-box-right h2{
  color:rgb(255, 255, 255);
  font-size: 30px;
  font-weight: 500;
}
.btn-box{
  width: 45%;
  padding: 10px 0px;
  /* border:1px solid; */
  display: flex;
  justify-content: space-between;
}
.coin-menu{
  width: 87%;
  margin: 8px auto;
  display: flex;
  justify-content: space-around;
  align-items: center;
  border-radius: 10px;
  /* background: linear-gradient(135deg,rgba(255, 255, 255, 0.16),rgba(255, 255, 255, 0.041));
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px); */
  padding: 10px 0px;
  
}
.coin-img{
  width:45px;
  height:45px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  /* border: 2px solid #0db067; */
  background: linear-gradient(135deg,rgba(255, 255, 255, 0.16),rgba(255, 255, 255, 0.041));
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  align-items: center;
  cursor: pointer;
}

.coin-img img{
  width: 70%;
  border-radius: 50%;
  padding: 2px;
}


.liquidity{
  width: 89%;
  margin: 80px auto 40px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.left-liquidity{
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items:flex-start;
}
.left-liquidity h1{
  font-weight: 700;
  font-size: 45px;
}
.liq-txt{
  background: linear-gradient(55deg, rgb(233, 8, 233) 0%, rgba(6, 78, 201, 0.895) 80%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.right-liquidity{
  width: 40%;
}
.right-liquidity > .right-liq-img{
  width: 100%;
}



.coin-container{
  width: 87%;
  margin: 3% auto;
  /* border: 1px solid; */
  
}
.coin-container-head h3{
  font-weight: 500;
  font-size: 28px;
}

.coin-conainer-bottom{
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit,minmax(23%,1fr));
  grid-auto-rows:1fr;
  grid-gap:10px;
  padding: 10px;
}





.coin-head{
  width: 95%;
  display: flex;
  align-items: center;
  /* border: 1px solid; */
  padding: 0px 5px;
  color: aliceblue;
}
.coin-head-img{
  width: 70px;
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  margin-right: 10px;
}
.coin-head-img img{
  border-radius: 50%;
  width: 100%;
}
.small-price{
  margin-top: -15px;
  color:#cd32f4;
}


.card-content{
  width: 95%;
  margin: 0px auto;
  padding: 0px 5px;
  margin-top: 5px;
  display: flex;
  justify-content:space-between;
}
.price-in-days{
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 13px;
  color: aliceblue;
}

.social-cap{
  width: 95%;
  padding: 0px 5px;
  margin-top: 5px;
  /* border: 1px solid; */
  color: aliceblue;
}
.site{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.market-cap{
  color: orange;
}




.about{
  width: 87%;
  /* border: 1px solid ; */
  margin: 5% auto;
}

.news-list{
  width: 95%;
  display: flex;
  flex-direction: column;
  gap:10px;
  /* border-radius: 10px; */
}
.news-card{
  border-radius: 10px;
}

.news-img{
  width: 35%;
  display: flex;
  justify-content: center;
  align-items: center;
  /* border: 1px solid; */
  border-radius: 10px;
  
}
.news-img img{
  width: 270px;
  height: 170px;
  border-radius: 10px;
}
.news-card{
  display: flex;
  /* justify-content: space-between; */
  gap:20px;
  padding: 10px;
  align-items:center;
  color: aliceblue;
}
.news-details{
  width: 80%;
}

.news-details h2{
  font-size: 20px;
  /* font-weight: 100; */
  color:rgb(191, 119, 255);
}

.know-more{
  margin-top: 20px;
  width: 20%;
  background: rgb(6, 198, 86);
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.know-more a{
  padding: 8px 14px;

  font-size: 16px;
  font-weight: 600;
  color: aliceblue;
  text-decoration: none;
}









.search{
  width: 87%;
  margin: 5% auto;
  display: flex;
  flex-direction: column;
  gap:20px;
}
.search h2{
  width: 95%;
  margin: 0px auto;
}
.search-input{
  width: 97%;
  margin: 0px auto;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.search-input input{
  width: 80%;
  padding: 13px 20px;
  border-radius: 5px;
  outline: none;
  border: none;
  font-size: 17px;
}


.search-result{
  width: 93%;
  margin: 20px auto;
  display: flex;
  flex-direction: column;
  gap:5px;
  align-items: center;
  /* background: white; */
  border-radius: 10px;
}
.search-coin-details{
  width: 100%;
  background: linear-gradient(135deg,rgba(255, 255, 255, 0.16),rgba(255, 255, 255, 0.041));
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 10px;
  padding: 8px;
  color: #f3ecf7;
}
.coin-name{
  display: flex;
  /* gap:10px; */
  align-items: center;
  width: 18%;
  font-size: 12px;
  /* color: #220a33; */
}
.img{
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
}
.img img{
  width: 100%;
}
.name{
  text-align: left;
  margin-left: 10px;
}
.coin-price , .coin-1h , .coin-1d, .coin-market{
  width: 15%;
  text-align: left;
}





.footer{
  width: 100%;
  margin: 60px auto 0px;
  display: flex;
  flex-direction: column;
  background-color: #42156085;
}
.top-sec{
  width: 90%;
  margin: 20px auto;
  display: flex;
  justify-content: space-between;
  text-align: left;
}
.footer-child{
  width: 30%;
  height: fit-content;
  /* color:#fbe2f4; */
}
.sec-1{
  width: 40%;
}
.site-link{
  width: auto;
  display: flex;
  flex-direction: column;
  gap:5px;
}
.site-link a{
  width: 30%;
  text-decoration: none;
  color: #38f1a4;
  font-size: 13px;
}
.site-link a:hover{
  text-decoration:underline;
}

.bottom-sec{
  width: 100%;
  margin: 10px auto 0px;
  background-color:#29054185 ;
  display: flex;
  justify-content: space-around;
  padding: 15px 0px;
  align-items: center;
}
.copyright{
  width: 40%;
  text-align: left;
  /* border: 1px solid; */
}
.social{
  width: 40%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.social a{
  text-decoration: none;
  color: #f3ecf7;
}







